import { registerBlockVariation } from '@wordpress/blocks';
import { addFilter } from '@wordpress/hooks';
import { InspectorControls } from '@wordpress/block-editor';
import { PanelBody, SelectControl, CheckboxControl, Spinner } from '@wordpress/components';
import { useSelect } from '@wordpress/data';

/** Event Query Block Variation - START */

// Register event query block variation
const eventsVariation = 'upcoming-events';

registerBlockVariation('core/query', {
  name: eventsVariation,
  title: 'Upcoming Events',
  description: 'Displays a list of upcoming events',
  icon: 'calendar-alt',
  attributes: {
    namespace: eventsVariation,
    query: {
      postType: 'events',
      perPage: 4,
      offset: 0,
      filterByDate: true
    },
  },
  isActive: ['namespace'],
  scope: ['inserter'],
  innerBlocks: [
    [
      'core/post-template',
      {},
      [
        ['core/post-title']
      ],
    ]
  ]
});

/** Event Query Block Variation - END */

/** Publications Query Block Variation - START */

const publicationsVariation = 'pubs-feed';

registerBlockVariation('core/query', {
  name: publicationsVariation,
  title: 'Publications Feed',
  description: 'Displays a feed of publications',
  icon: 'list-view',
  attributes: {
    namespace: publicationsVariation,
    query: {
      postType: 'publications',
      perPage: 4,
      offset: 0,
      // Add a specific attribute for this exclusion
      taxQueryExcludePubs: [],
    },
  },
  isActive: ['namespace'],
  scope: ['inserter'],
  innerBlocks: [
    [
      'core/post-template',
      {},
      [
        ['core/post-title']
      ],
    ]
  ]
});


// This section adds the inspector controls specifically to the Publications variation
const isPubsVariation = (props) => {
  const { attributes: { namespace } } = props;
  return namespace && namespace === publicationsVariation;
};

const PubVariationControls = ({ props: { attributes, setAttributes } }) => {
  const { query } = attributes;
  const { taxQueryExcludePubs = [] } = query;

  // Fetch only 'publication_category' terms
  const { terms, isLoading } = useSelect((select) => ({
      terms: select('core').getEntityRecords('taxonomy', 'publication_category', { per_page: -1 }),
      isLoading: !select('core').hasFinishedResolution('getEntityRecords', ['taxonomy', 'publication_category', { per_page: -1 }]),
  }), []);

  // Handler to update attributes when checkboxes are changed
  const toggleTerm = (termId) => {
    const newExcludedTerms = taxQueryExcludePubs.includes(termId)
      ? taxQueryExcludePubs.filter(id => id !== termId)
      : [...taxQueryExcludePubs, termId];
    setAttributes({ query: { ...query, taxQueryExcludePubs: newExcludedTerms } });
  };

  return (
    <>
      <PanelBody title="Publication Feed Settings">
        {/* Language Selector */}
        <SelectControl
          label="Language"
          value={query.language}
          options={[
            { value: '', label: '' },
            { value: '1', label: 'English' },
            { value: '2', label: 'Spanish' }
          ]}
          onChange={(value) =>
            setAttributes({ query: { ...query, language: value } })
          }
        />
      </PanelBody>

      <PanelBody title="Exclude Publication Categories">
        {isLoading && <Spinner />}
        {!isLoading && terms && terms.length === 0 && <p>No categories found.</p>}
        {!isLoading && terms && terms.map(term => (
          <CheckboxControl
            key={term.id}
            label={term.name}
            checked={taxQueryExcludePubs.includes(term.id)}
            onChange={() => toggleTerm(term.id)}
          />
        ))}
      </PanelBody>
    </>
  );
};

export const withPubVariationControls = (BlockEdit) => (props) => {
  return isPubsVariation(props) ? (
    <>
      <BlockEdit {...props} />
      <InspectorControls>
        <PubVariationControls props={props} />
      </InspectorControls>
    </>
  ) : (
    <BlockEdit {...props} />
  );
};

addFilter('editor.BlockEdit', 'core/query/with-pub-controls', withPubVariationControls);

/** Publications Query Block Variation - END */


/** Stories Query Block Variation - START */

